.header {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 34vw;
  /* border: 1px solid black; */
}

.header__logo {
  display: flex;
  height: 72%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.header__logo img {
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  /* border: 1px solid black; */
}

.header__slogan {
  height: 16%;
  width: 100%;
  display: flex;
  justify-content: center;
  /* border: 1px solid black; */
}

.header__slogan h4 {
  font-style: italic;
  font-size: 4vw;
  color: #707070;
}

.header__current-date h4 {
  display: flex;
  justify-content: flex-end;
  right: 0;
  margin-right: 6vw;
  font-size: 3.2vw;
  font-weight: 400;
  color: #707070;
}

/* For tablets */
@media only screen and (min-width: 600px) {
  .header {
    height: 28vw;
  }

  .header__current-date h4 {
    font-size: 2.8vw;
  }

  .header__slogan h4 {
    font-size: 3.6vw;
  }
}
